import {target} from './config'
console.log('target', target)
let Service = {
    //url
    BaseURL: target + '/api/v1',

    // url
    getReqUrl: function(){
        return {
            signin: this.BaseURL + "/accesstoken",     // (post) 
            getTopics: this.BaseURL + "/topics",     // (get)
            getUnreadCount: this.BaseURL + "/message/count", // (get)
            getUserDetail: this.BaseURL + "/user/",  // (post)
            getMsg: this.BaseURL + "/messages" ,     //  (get)
            createTopics: this.BaseURL + "/topics", // (post)
            topicDetail: this.BaseURL + "/topic/", // (get)
            createComment: (topicId) => this.BaseURL + `/topic/${topicId}/replies`, // (post)
            clickLike: (replyId) => this.BaseURL + `/reply/${replyId}/ups`  // (post)
        }
    },

    // 
    localItem: function (key, value) {
        if (arguments.length == 1) {
            return localStorage.getItem(key);
        } else {
            return localStorage.setItem(key, value);
        }
    },

    //
    removeLocalItem: function (key) {
        if (key) {
            return localStorage.removeItem(key);
        }
        return localStorage.removeItem();
    },

    //
    clearAllLocal: function(){
        return localStorage.clear();
    },

    //
    formatDate: function (str) {
        var date = new Date(str);
        var time = new Date().getTime() - date.getTime(); //- =  = 
        if (time < 0) {
            return '';
        } else if (time / 1000 < 60) {
            return '';
        } else if ((time / 60000) < 60) {
            return parseInt((time / 60000)) + '';
        } else if ((time / 3600000) < 24) {
            return parseInt(time / 3600000) + '';
        } else if ((time / 86400000) < 31) {
            return parseInt(time / 86400000) + '';
        } else if ((time / 2592000000) < 12) {
            return parseInt(time / 2592000000) + '';
        } else {
            return parseInt(time / 31536000000) + '';
        }
    },

    //
    isEmptyObj(obj){
     // because Object.keys(new Date()).length === 0;
     // we have to do some additional check
       return Object.keys(obj).length === 0 && obj.constructor === Object;
    }

};

export default Service;



